<template>
    <div
        :style="{
            top: `${position.y}%`,
            left: `${position.x}%`,
            zIndex: active ? 1000 : position.y,
        }"
        class="monster"
    >
        <div
            v-if="!correct"
            class="image enabled"
            @click="onImageClick"
        >
            <img :src="`./img/monster/${monster}.png`" />
        </div>
        <div
            v-else
            class="image"
        >
            <img src="../assets/icon/skull.png" />
        </div>
        <div
            v-if="active"
            :class="{correct, incorrect}"
            class="answer"
        >
            <input
                v-model="answer"
                ref="input"
                :disabled="correct"
                class="answer-input"
                type="text"
                role="textbox"
                autocapitalize="off"
                autocomplete="off"
                autocorrect="off"
                spellcheck="false"
                @keypress="onKeyPress"
            />
            <button
                class="submit"
                @click="submit"
            >
            </button>
        </div>
    </div>
</template>


<script>
export default {
    name: 'Answer',
    props: {
        index: {
            type: Number,
        },
        word: {
            type: String,
            default: null,
        },
        active: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            answer: '',
            synth: window.speechSynthesis,
            submitted: false,
            position: {
                x: Math.floor(Math.random() * 70 + 10),
                y: Math.floor(Math.random() * 50 + 10),
            },
            monster: [
                'abomination',
                'boar',
                'cultist_goblin',
                'cyclop',
                'demon_soldier',
                'guard_goblin',
                'hermit',
                'imp',
                'lizardman',
                'lurker',
                'magma_demon',
                'marsh_howler',
                'metal_golem',
                'minion',
                'orc',
                'overlord',
                'raptor',
                'scorpion',
                'sharpbeak',
                'skeleton',
                'slug',
                'snake',
                'stone_spirit',
                'swarm_soldier',
                'toad',
                'troll',
                'werewolf',
                'wood_spider',
                'yeti',
            ][Math.floor(Math.random() * 29)],
        };
    },
    computed: {
        correct() {
            return this.submitted && this.answer === this.word;
        },
        incorrect() {
            return this.submitted && this.answer !== this.word;
        },
    },
    methods: {
        readWord() {
            if (this.synth) {
                this.synth.cancel();
            }
            const voice = this.synth.getVoices().find((voice) => voice.lang == 'fr-FR');
            var utterThis = new SpeechSynthesisUtterance(this.word);
            utterThis.voice = voice;
            this.synth.speak(utterThis);
        },
        submit() {
            this.submitted = true;
            if (this.correct) {
                this.$emit('correct', this.index);
            }
        },
        onKeyPress(event) {
            if (['Enter', 'NumpadEnter'].includes(event.code)) {
                this.submit();
            } else {
                this.submitted = false;
            }
        },
        focus() {
            this.$emit('focus', this.index);
            this.$nextTick(() => {
                this.$refs.input.focus();
            });
        },
        onImageClick() {
            this.readWord();
            this.focus();
        },
    },
}
</script>

<style lang="scss">
.monster {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20em;
    pointer-events: none;

    .image {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 300px;

        &.enabled {
            cursor: pointer;
            pointer-events: auto;
        }
    }

    .answer {
        height: 3em;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        pointer-events: auto;
        z-index: 1000;

        button,
        input {
            margin: 0 0.5rem;
        }

        button.submit {
            background-image: url(../assets/icon/sword.png);
            border: 3px #222222;
            border-style: outset;
            background-size: contain;
            min-width: 3em;
            display: block;
            padding: 0;
            font-size: 1rem;

            &:active {
                border-style: inset;
            }
        }

        .answer-input {
            font-size: 1.5em;
            flex-grow: 1;
            padding: 0.25rem;
        }

        &.correct::after {
            content: '✔';
            color: green;
        }

        &.incorrect::after {
            content: '❌';
            color: red;
        }
    }
}
</style>