<template>
  <div
    id="app"
    class="app-root"
    :class="background"
  >
    <question
      v-if="showQuestion"
      @update-words="updateWords"
      @hide="hideQuestion"
    />
    <div
      v-else
      class="answers"
    >
      <answer
        v-for="(word, index) in words"
        :key="index"
        :index="index"
        :word="word"
        :active="index === focusedAnswer"
        @focus="focusAnswer"
        @correct="onCorrectAnswer"
      />
    </div>
    <div
      v-if="message !== null"
      class="message"
    >
      {{message}}
    </div>
  </div>
</template>

<script>
import Answer from './components/Answer.vue'
import Question from './components/Question.vue'

export default {
  name: 'app',
  components: {
    Answer,
    Question,
  },
  data() {
    return {
      words: [],
      nbCorrect: 0,
      showQuestion: true,
      focusedAnswer: null,
      message: null,
      background: ['cave', 'desert', 'grassland'][Math.floor(Math.random() * 3)],
    }
  },
  methods: {
    updateWords(words) {
      this.words = words;
    },
    hideQuestion() {
      this.showQuestion = false;
    },
    focusAnswer(index) {
      this.focusedAnswer = index;
    },
    onCorrectAnswer() {
      this.focusedAnswer = null;
      this.nbCorrect++;

      if (this.nbCorrect == this.words.length) {
        this.message = 'BRAVO !';
      }
    }
  },
};
</script>

