<template>
    <div class="question">
        <textarea
            v-model="question"
            autocapitalize="off"
        />
        <button
            @click="hide"
        >OK</button>
    </div>
</template>


<script>
export default {
    name: 'Question',
    props: {
    },
    data() {
        return {
            question: '',
        };
    },
    computed: {
        words() {
            return this.question.split("\n").filter(word => word !== '');
        },
    },
    methods: {
        hide() {
            this.$emit('hide');
        },
    },
    watch: {
        words() {
            this.$emit('update-words', this.words);
        },
    },
}
</script>

